import React from "react";
import PropTypes from "prop-types";

export default function NavBar(props) {
  return (
    <nav
      className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}
    >
      <div className="container-fluid">
        <a className="navbar-brand" href="/">
          {props.title}
        </a>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="/navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        {/* <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <a className="nav-link active" aria-current="page" href="/">
                {props.main}
              </a>
            </li>

            <li className="nav-item">
              <a className="nav-link" href={props.aboutLink}>
                About
              </a>
            </li>
          </ul>
          <div className={`form-check form-switch text-${props.mode === "light" ? "dark" : "light"}`}>
            <input type="checkbox" id="flexSwitchCheckDefault" className="form-check-input" onClick={props.toggleMode} />
            <label htmlFor="flexSwitchCheckDefault" className="form-check-label">Enable {props.mode === "light" ? "Dark" : "Light"} Mode</label>
          </div>
        </div> */}
      </div>
    </nav >
  );
}
NavBar.propTypes = {
  title: PropTypes.string.isRequired,
  aboutLink: PropTypes.string,
  main: PropTypes.string,
};
NavBar.defaultProps = {
  title: "CodeUtiles",
  main: "Home",
};
