import React, { useState } from "react";

export default function TextForm(props) {
    const [text, setText] = useState("");


    const clickHandler = () => {
        let newText = text.toUpperCase(text);

        setText(newText);
    };
    const handleCopy = () => {
        let myBox = document.getElementById('myBox');
        navigator.clipboard.writeText(text);
        myBox.select();
    }
    const extraSpaces = () => {
        setText(text.split(/[ ]+/).join(" "))
    }
    const handleLowClick = () => {
        let newText = text.toLowerCase(text);

        setText(newText);
    };
    const handleOnChange = (event) => {


        setText(event.target.value);

    };
    const clearAll = () => {
        setText("");

    }
    const codeCompressor = () => {
        let compressor = text.replace(/(\r\n|\n|\r)/gm, "");
        compressor = compressor.trim().split(/ +/).join(' ');


        setText(compressor);
    }
    const codePrettier = () => {
        let prettier = text.trim().split(/ +/).join(' ');
        prettier = text.replace(/(;)/g, ";\n");
        prettier = prettier.replace(/{/g, "{\n");
        prettier = prettier.replace(/}/g, "}");
        prettier = prettier.replace(/</g, "\n<");


        setText(prettier);
    }
    return (
        <>
            <div className="my-3 container" >
                <h4>{props.title}</h4>
                <textarea
                    style={{ background: props.mode === "light" ? "white" : "rgb(0, 42, 62)", color: props.mode === "light" ? "black" : "white" }}
                    value={text}
                    className="form-control"
                    id="myBox"
                    rows="9"
                    onChange={handleOnChange}
                    placeholder="Enter Your Text Here..."
                ></textarea>
                <button className="btn btn-primary my-3 mx-1" onClick={clickHandler}>Convert To Upper Case</button>
                <button className="btn btn-primary my-3 mx-1" onClick={handleLowClick}>Convert To Lower Case</button>
                <button className="btn btn-primary my-3 mx-1" onClick={codeCompressor}>Code Compressor</button>
                <button className="btn btn-primary my-3 mx-1" onClick={codePrettier}>Code Prettier</button>
                <button className="btn btn-primary my-3 mx-1" onClick={handleCopy}>Copy Text</button>
                <button className="btn btn-primary my-3 mx-1" onClick={extraSpaces}>Remove Extra Spaces</button>
                <button className="btn btn-danger my-3 mx-1" onClick={clearAll}>Clear All</button>

            </div>
            <div className="container">
                <h2>Your Text Summary</h2>
                <p>Characters: {text.length}</p>
                <p>Words: {text.length === 0 ? 0 : text.trim().split(' ').length}</p>

                < h3 > Preview</h3>
                <p>{text.length > 0 ? text : "Type Something to Preview..."}</p>
            </div>

        </>

    );
}
